body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
    background: #27a69a;
    color: #ffffff;
    border: 0px;
    border-radius: 5px;
    padding: 1em;
    display: block;
    width: 100%;
    max-width: 250px;
    margin: 0 auto 1.5em auto;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
  }
  
button img {
    width: 25px;
    margin-right: 5%;
}
  
#btn-google {
    background: white;
    color: #444;
    border: thin solid #888;
}
.cardsContainer{
    max-width: 80%;
    display: flex;
    flex-wrap: wrap;
    margin: 40px auto 0 auto;
    justify-content: space-evenly;
}

.pokemonCard {
    padding-bottom: 1em;
    margin-bottom: 1em;
    min-width: 30%;
    max-width: 30%;
    flex: 1 1;
}

